.App {
  text-align: center;

}

.searchBar-container {
  padding: 10px;
  border: 10px red;
  background-color: green;
  display:flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(pink, rgb(226, 239, 240));

}

.searchBar-button{
  padding: 5px;
  border: 5px;
  margin: 10px;
  font-size: large;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif ;
  border-radius: 10px;
  background-color: beige;

  
}

.searchBox{
  border-radius: 10px;
  color: black;
  background-color: beige;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
